<template>
	<div class="pageWrap navBarPadding" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="navBarBox" ref="navBarHeight">
            <img class='navLeftArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goBack()'>
            <p class='navTitle'>About</p>
        </div>
        <div class="orderDetailWrap">
            <div class="aboutTop flexCenter">
                <img class='aboutLogo' src="../assets/images/aboutLogo.png" alt="">
                <p class="versionText">Version 1.0.4</p>
            </div>
            <div class="aboutBottom">
                <div class="aboutBotItem flexBetween">
                    <p class="leftName">General information</p>
                    <p class="rightText">info@youlike.video</p>
                </div>
                <div class="aboutBotItem flexBetween">
                    <p class="leftName">Report content</p>
                    <p class="rightText">report@youlike.video</p>
                </div>
                <div class="aboutBotItem flexBetween">
                    <p class="leftName">Terms of</p>
                    <img class='blackRightArrwo' src="../assets/images/blackRightArrwo.png" alt="">
                </div>
                <div class="aboutBotItem flexBetween">
                    <p class="leftName">Privacy policy</p>
                    <img class='blackRightArrwo' src="../assets/images/blackRightArrwo.png" alt="">
                </div>
            </div>
        </div>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'About',
		data: function(){
			return {
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                sysType: 'android'
			}
		},
		created(){
			// this.getWithdrawData()
            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }

			let u = navigator.userAgent
			let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
			let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
			if (isAndroid) {
				this.sysType = 'android'
			}
			if (isIOS) {
				this.sysType = 'ios'
			}

            this.getVersionData()
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getVersionData: function(){
                this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				let params = {
					platform: this.sysType
				}
				https.fetchGet('/api/user/app',params).then((data) => {
					console.log(data)
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						if(res.data && res.data.file) {
							// this.setFileData(res.data.file)
						}else {
							// this.setFileData('')
						}
					}else {
						this.$toast.loading({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className:'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getVersionData()
					}
				})
			},
            
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
